<template>
  <section>
    <loader :is-visible="isLoading"></loader>
    <div class="right-content right-content-expand">
      <div class="col-lg-12 float-left">
        <div class="tab-content" id="myTabContent">
          <div class="tab-pane fade border-0 p-0 active show no-border-mb" id="Overview" role="tabpanel" aria-labelledby="dashboard-tab">
            <div class="clearfix"></div>
            <div class="listing mt-2">
              <div class="row">
                <div class="col-md-12">
                  <div class="card mb-3 probx">
                    <div class="card-header barrw">
                      <h2>Requisition Details</h2>
                      <div class="cont_right ">
                        <div class="bar">
                          <a title="Back to List" href="#" @click.prevent="goBack" class="artlist btn btn-secondary text-white">
                            <i class="fa fa-angle-double-left pr-2"></i>Back to List
                          </a>
                        </div>
                      </div>
                    </div>
                    <div class="card-body">
                      <div class="row" v-if="JobData.length > 0">
                        <div class="col-md-4 col-lg-3" v-for="(header, index) in Headers" :key="index">
                          <div class="form-group">
                            <label><b>{{ header.DISPLAY_NAME }}:</b> {{ JobData[0][header.COLUMN_NAME] }}</label>
                          </div>
                        </div>
                      </div>
                      <div v-else>
                        <p>No data available</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <!-- end right panel content -->
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>


<script>
import Schema from '../JobPost/Schema';
import DataService from '../../../services/DataService';

export default {
  data() {
    return {
      isLoading: false,
      JobData: [],
      Headers: [],
      userType: '',
      requisitionId: 0
    };
  },
  created() {
    var vm = this;
    vm.requisitionId = this.$route.params.requisitionId;
    console.log("Requisition ID:", vm.requisitionId);  // Debug log
    vm.FetchData();
  },
  methods: {
    FetchData() {
      var vm = this;
      const params = `userType=${vm.userType}&requisitionId=${vm.requisitionId}`;
      this.isLoading = true;
      DataService.RequisitionDetail(params).then(response => {
        console.log("Response Data:", response.data);  // Debug log
        if (response.data && response.data.data && response.data.data.length > 0) {
          this.Headers = response.data.schema[0];  // Adjusted to access schema correctly
          this.JobData = response.data.data;
          console.log("Headers:", this.Headers);  // Debug log
          console.log("JobData:", this.JobData);  // Debug log
        }
        this.isLoading = false;
      }).catch(error => {
        console.error('Error fetching data:', error);
        this.isLoading = false;
      });
    },
    goBack() {
      this.$router.push({ name: 'JobRequisitionList' });
    }
  }
}
</script>

